.cardMovie{
  display: flex;
  flex-direction: column;
  box-shadow:  0 0 13px -1px rgba(0,0,0,0.75);
  border-radius: 7px;
  color: whitesmoke;
  text-shadow: 1px 1px 2px #000000;
  width: 200px;
  height: 300px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-top: 0.4rem;
  margin-bottom: 0.5rem;
}

.cardMovie:hover{
  -webkit-box-shadow: 0 0 13px -1px rgba(0,252,135,0.65);
  -moz-box-shadow: 0 0 13px -1px rgba(0,252,135,0.65);
  box-shadow: 0 0 13px -1px rgba(0,252,135,0.65);
}

@media screen and (max-width: 800px) {
  .cardMovie {
    width: 150px;
    height: 250px;
  }
}
